import React, { Component } from "react";
import "../scss/section.scss";
// import * as actionTypes from "../store/actions";
import { connect } from "react-redux";
import Title from "../elements/Title/Title";
import { Element } from "react-scroll";

class Reservation extends Component {
  render() {
    return (
      <Element name="rezervation">
        <div className="margin-80">
          <Title name="REZERWACJA" />
          <div className="firstTextWidth textColor">
            <p>
              Jak na ten moment o dostpne terminy prosimy dopytywa si
              telefonicznie pod numerem telefonu 666-196-075. Kwot za najem
              lokalu naley uici jak najszybciej po jego rezerwacji - kart i
              gotwk (na Struga 18) lub przelewem. Wszystkie niezbdne dane do
              przelewu bd mieli Pastwo podane w umowie. Nasz programista jest
              na etapie tworzenia aplikacji, dziki ktrej bdziecie widzieli
              kalendarz zarwno z dostpnymi jak i zajtymi terminami. Oprcz
              tego, bdziecie mogli tez od razu opaci rezerwacje, dziki czemu
              bdziecie mieli pewno, ze nikt nie zarezerwuje terminu nagle
              przed Wami. Wszystko ju wiecie? Jeli tak to dziaamy -
              zapraszamy do wsppracy.
            </p>
          </div>
        </div>
      </Element>
    );
  }
}

const mapStateToProps = state => {
  return {};
};
const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(Reservation);
